<template>
    <div class="login">
        <div class="top">
            <b>学校实验室仓管系统</b>
            <div></div>
            <span>欢迎登录</span>
        </div>
        <div class="banner">
            <img src="../assets/login_pic.png" alt="">
            <div class="formData">
                <form action="">
                    <div class="formTitle">账号登录</div>
                    <div class="input-group">
                        <span><i class="iconfont el-icon-pharmaceutical_administration-zhanghao"></i></span>
                        <input type="text" placeholder="账号" @blur="inputBlur('name',ruleForm.name)"
                               v-model="ruleForm.name">
                        <p style="position:absolute;left:45px;top: 45px;font-size:12px;color:red;">{{ruleForm.userError}}</p>
                    </div>
                    <div class="input-group">
                        <span><i class="iconfont el-icon-pharmaceutical_administration-icon2"></i></span>
                        <input type="password" placeholder="密码" @blur="inputBlur('password',ruleForm.password)"
                               v-model="ruleForm.password" @keyup.enter="submitForm(ruleForm.name,ruleForm.password)">
                        <p style="position:absolute;left: 45px;top: 45px;font-size:12px;color:red;">
                            {{ruleForm.passwordError}}</p>
                    </div>
                    <!--<template>-->
                        <!--&lt;!&ndash; `checked` 为 true 或 false &ndash;&gt;-->
                        <!--<el-checkbox v-model="checked">记住密码</el-checkbox>-->
                    <!--</template>-->
                    <button type="button" @click="submitForm(ruleForm.name,ruleForm.password)"
                            :disabled='ruleForm.beDisabled' :plain="true">登录
                    </button>
                </form>
            </div>
        </div>
        <!--<div class="btm">-->
            <!--<p>Copyright © 2018-2022 Jootu. All rights reserved.</p>-->
            <!--<p>浙公网安备 33040202000255号</p>-->
            <!--<p>浙ICP备17028990号-2</p>-->
        <!--</div>-->
    </div>
</template>
<script>
    export default {
        name: "login",
        data() {
            return {
                checked: false,
                ruleForm: {
                    name: "",
                    userError: "",
                    password: "",
                    passwordError: "",
                    beDisabled: false
                }
            };
        },
        methods: {
            inputBlur: function (errorItem, inputContent) {
                if (errorItem === "name") {
                    if (inputContent === "") {
                        this.ruleForm.userError = "用户名不能为空!";
                    } else {
                        this.ruleForm.userError = "";
                    }
                } else if (errorItem === "password") {
                    if (inputContent === "") {
                        this.ruleForm.passwordError = "密码不能为空!";
                    } else {
                        this.ruleForm.passwordError = "";
                    }
                }
            },
            submitForm(name, password) {
                let _this = this;
                if (name == "" || password == "") {
                    this.ruleForm.userError = "用户名不能为空!";
                    this.ruleForm.passwordError = "密码不能为空!";
                } else {
                    this.$axios.post("/login", {
                        name: this.ruleForm.name,
                        password: this.ruleForm.password
                    }).then(response => {
//                        console.log(response)
                        if (response.data.status_code == "200") {
                            let obj = response.data.data;
                            obj.token = response.data.token;
                            localStorage.setItem("user", JSON.stringify(obj));
                            if(response.data.data.is_admin == 1){
                                _this.$router.push("/manifest");
//                                _this.$router.push("/addlist");
                            } else {
                                _this.$router.push("/homepage");
                            }
                        } else {
                            this.$message.error('你输入的用户名或密码不正确');
                            this.ruleForm.name = '';
                            this.ruleForm.password = '';
                        }
                    });
                }
            }
        }
    };
</script>
<style scoped lang="less" rel="stylesheet/less">
    * {
        margin: 0;
        padding: 0;
    }

    html {
        width: 100%;
        height: 100%;
    }

    body {
        width: 100%;
        height: 100%;
        margin: 0;
    }
    #app, html {
        overflow: auto!important;
        width: 100%;
        height: 100%;
    }
    .login {
        overflow: auto;
        height: 100%;
        width: 100%;
        min-width: 1440px;
        .top {
            display: inline-block;
            margin-left: 187px;
            padding-top: 80px;
            margin-bottom: 27px;
            b {
                width: 387.1px;
                height: 50px;
                font-family: PingFangSC;
                font-size: 36px;
                font-weight: 600;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;
                text-align: left;
                color: #5ca8e7;
            }
            div {
                display: inline-block;
                border: 0.5px solid #979797;
                height: 30px;
                position: relative;
                top: 5px;
                margin: 0 10px;
            }
            span {
                width: 171.4px;
                height: 33px;
                font-family: PingFangSC;
                font-size: 24px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;
                text-align: left;
                color: #868586;
                position: relative;
                top: -4px;
            }
        }
        .banner {
            width: 100%;
            height: 519px;
            background-color: #5ca8e7;
            margin-bottom: 100px;
            img {
                position: relative;
                left: 204px;
                top: 85px;
            }
        }
        .formData {
            width: 360px;
            height: 450px;
            float: right;
            margin-right: 200px;
            margin-top: 33px;
            form {
                width: 360px;
                height: 450px;
                border: 1px solid #5ca8e7;
                border-radius: 5px;
                /*position: relative;*/
                /*left: 1200px;*/
                /*top: -470px;*/
                background-color: white;
                .formTitle {
                    font-size: 18px;
                    font-family: PingFangSC;
                    color: #4a4a4a;
                    height: 40px;
                    text-align: center;
                    line-height: 40px;
                    background-color: rgba(0, 0, 0, 0.1);
                }
                .input-group:nth-child(2) {
                    position: relative;
                    top: 42px;
                }
                .input-group:nth-child(3) {
                    position: relative;
                    top: 70px;
                }
                .input-group {
                    border-radius: 3px;
                    width: 270px;
                    height: 40px;
                    display: block;
                    margin: 0 auto;
                    border: solid 1px #b9b7b7;
                    span {
                        display: inline-block;
                        width: 39px;
                        height: 40px;
                        border-right: 1px solid #b9b7b7;
                        i {
                            display: block;
                            text-align: center;
                            line-height: 40px;
                            font-size: 20px;
                            color: #5ca8e7;
                        }
                    }
                    input {
                        font-size: 16px;
                        /*text-indent: 15px;*/
                        border: 0;
                        padding-left: 15px;
                        outline: none;
                        width: 205px;
                        height: 35px;
                        vertical-align: middle;
                        margin-bottom: 2px;
                    }
                }
                .el-checkbox {
                    position: relative;
                    top: 80px;
                    left: 46px;
                }
                button {
                    cursor: pointer;
                    outline: none;
                    position: relative;
                    top: 182px;
                    left: -35px;
                    width: 270px;
                    height: 40px;
                    border-radius: 22.5px;
                    background-color: #5ca8e7;
                    box-shadow: 0 2px 4px 0 rgba(92, 168, 231, 0.61);
                    font-size: 18px;
                    color: #ffffff;
                    margin-left: 75px;
                    border: 0;
                }
            }
        }
    }

    .btm {
        display: block;
        height: 64px;
        margin-top: 46px;
        text-align: center;
        p {
            font-family: PingFangSC;
            font-size: 13px;
        }
    }
</style>





